
import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
// import { withRouter } from 'react-router-dom';
import { actions } from '../../redux/actions/action';
import { Button } from 'react-bootstrap'
import Table from 'react-bootstrap/Table'
// import { Formik, Field, Select, Form } from 'formik';
import Form from 'react-bootstrap/Form'
import { AddEdit } from '../AddEdit'
import NewProduct from '../product/NewProduct';
import Scroll from '../Scroll';
import Search from '../Search'
import Modal from 'react-bootstrap/Modal'
// import BootstrapTable from 'react-bootstrap-table-next';
// omit...
import '../../App.css'
import $ from 'jquery'
function ProductList_manager(props) {

  // const [isAddMode, setIsAddMode] = useState(true);

  const [show, setShow] = useState(false);
  const [idToDelete, setIdToDelete] = useState()
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const { products } = props;
  const { categories } = props
  const [data, setData] = useState([]);
  const [categoryList, setCategoryList] = useState([]);
  const [productToEdit, setProductToEdit] = useState()
  const [sortType, setSortType] = useState('hebrewName');
  let productE
  // const [isLoaded, setIsLoaded] = useState(false);
  // const [error, setError] = useState(null);

  if (!props.products || !props.products.length) {
    props.getAllProducts()

  }

  if (!categories || !categories.length) {

    props.getAllCategories()

  }
  useEffect(() => {


    console.log(sortType);
    console.log(products);
    const sortArray = type => {

      const types = {
        hebrewName: 'hebrewName',
        description: 'description',
        available: 'available',
      };
      const sortProperty = types[type];
      // if (categoryList.length)
      //   setCategoryList(products)
      const sorted = [...products].sort((a, b) => {
        var regex = /^[a-zA-Z]+$/;
        if ([sortProperty] != "" && [sortProperty] != undefined) {

          //debugger
          if (a[sortProperty] === true || b[sortProperty] === true || b[sortProperty] === false || a[sortProperty] === false) {
            return a[sortProperty] - b[sortProperty];
          }

          else
            // if (a[sortProperty] != "" && a[sortProperty] != undefined && b[sortProperty] != "" && b[sortProperty] != undefined)
            if (sortProperty.match(regex)) {
              console.log(sortProperty);
              return a[sortProperty].localeCompare(b[sortProperty]);
            }
            else {

              return b[sortProperty] - a[sortProperty];
            }
        }

      });
      setData(sorted);
      setCategoryList(data)
    };

    sortArray(sortType);

    if ($) {


      $(".clearButton").on("click", function () {


        $(this).siblings().val('')
        $(this).addClass('d-none')

      });


      $(".itemInput").on("input", function () {

        $(this).siblings().removeClass('d-none')

        if ($(this).val() == "") {
          $(this).siblings().addClass('d-none')
        }
      });
    }
  }, [sortType, props, productToEdit]);


  const changeCategory = async (event) => {

    let categoryId = event.target.value
    if (categoryId == "selectCategory")
      setCategoryList(data)
    else {
      let list = await data.filter(x => {
        return x.categoryID == categoryId;
      })
      setCategoryList(list)
    }

    console.log("list:::::::::::" + categoryList.length)

  }



  const editItem = async (product) => {
    //debugger
    // await setProductToEdit(product)
    // alert(productToEdit ? productToEdit._id : "jj")


    // $('.newId_').val(product._id)
    // $('.newName_').val(product.name)
    // $('.newHebrewName_').val(product.hebrewName)
    // $('.newDescription_').val(product.description)
    // $('.newHebrewDescription_').val(product.hebrewDescription)
    // $('.newPrice_').val(product.price)





    $('#newId').val(product._id)
    $('#newName').val(product.name)
    $('#newHebrewName').val(product.hebrewName)
    $('#newDescription').val(product.description)
    $('#newHebrewDescription').val(product.hebrewDescription)
    $('#newPrice').val(product.price)
    $('#newCategory').val(product.categoryID)
    $('#newAvailable').prop('checked', product.available == true ? false : true)
    $('#newDisplay').prop('checked', product.display == true ? false : true)





  }
  function saveItem(id) {


    let product = {
      "_id": id,
      "name": $('#' + id + ' td #name').val() != "" ? $('#' + id + ' td #name').val() : $('#' + id + ' td #name').attr('placeholder'),
      "description": $('#' + id + 'td #description').val() != "" ? $('#' + id + ' td #description').val() : $('#' + id + ' td #description').attr('placeholder'),
      // "img":$('#' + id + ' td #description').val(),
      "available": $('#' + id + ' td #available').val() != "" ? $('#' + id + ' td #available').val() : $('#' + id + ' td #available').attr('placeholder'),
      "categories": $('#' + id + ' td #categories').val() != "" ? $('#' + id + ' td #categories').val() : $('#' + id + ' td #categories').attr('placeholder')
    }
    console.log(product);
    props.updateProduct(product)

  }
  function copyItem(id) {
    props.copyProduct(id)


  }

  function openDeleteMoodal(id) {
    setShow(true)
    setIdToDelete(id)
    // console.log("toDelete: " + id);

  }
  function deleteProduct() {

    props.deleteProduct(idToDelete)
    setShow(false)
  }
  function openForm() {
    if ($('.NewProduct').hasClass("d-none")) {
      $('.NewProduct').removeClass("d-none")
      $('.productList').removeClass('col-md-12').addClass('col-md-9')
    }

  }





  const firstLine = Array.isArray(products) && products.length ? products[0] : {};
  const headers = Object.keys(firstLine);

  return (

    <div className='container   pb-0'>
      <Modal show={show} onHide={handleClose} animation={false}>
        <Modal.Header closeButton className='rtl'>
          <Modal.Title></Modal.Title>
        </Modal.Header>
        <Modal.Body className="text-center border-0">?        </Modal.Body>
        <Modal.Footer className="d-flex justify-content-center">
          <Button variant=" secondary" className='' onClick={handleClose}>
            
          </Button>
          <Button className='btn goldButton ' onClick={deleteProduct}>
            
          </Button>
        </Modal.Footer>
      </Modal>
      {/* <h1> </h1> */}
      <div className='row  rtl mt-2' style={{ height: '800px !important' }}>
        {/* <Search details={products} /> */}


        <div className=' productList col-md-7 p-3 bg-light'  >
          {/* <button onClick={e => openForm()}>adddddd</button> */}
          <div className='row d-flex titles  mb-5'>
            <div className='col-6  text-end'>: {categoryList.length} </div>
            <div className='col-6 text-start row d-flex'>
              <div className='col-md-6'>
                {/* <Form.Label class="mb-1 lableForm"></Form.Label> */}
                <Form.Select aria-label="Default select example" className="rounded-0 w-fitCon py-1" required onChange={(e) => changeCategory(e)}>
                  <option value="selectCategory"> </option>
                  {
                    categories.map((category) => <option key={category._id} value={category._id}>{category.hebrewName}
                    </option>)
                  }
                </Form.Select>
              </div>
              <div className='col-md-6'>
                <input placeholder=' ' className='w-100 inputOf_Search bg-transparent border-0 border-bottom border-dark' />
              </div>

            </div>

          </div>


          {/* <Table className='border-none' size="sm" >

            <div className='row mr-4 mb-3 ltr' key={"header"}>
              <div className='col-4  lableForm text-center pl-5'></div>
              <div className='col-2 lableForm' value="createDate" id="createDate" onClick={e => setSortType("createDate")}> </div>
              <div className='col-2 lableForm' value="available" id="available" onClick={e => setSortType("available")}></div>
              <div className='col-2 lableForm' value="price" id="price" onClick={e => setSortType("price")}></div>

              <div className='col-2 lableForm' value='hebrewName' id='hebrewName' onClick={e => setSortType('hebrewName')}> </div>

            </div>


            <div className="overflow-auto border-0 productList-manager ltr" style={{ height: '480px' }}>

              <tbody id="tableBody" >
                {categoryList.map((item) => (
                  <tr className='row m-auto bg-white mb-3 ' key={item._id} id={item._id}>
                    <td className='col-4 '>
                      <td className=' ' onClick={() => openDeleteMoodal(item._id)}><i class="fas fa-trash-alt "></i></td>
                      <td className=''>|</td>
                      <td className=' ' onClick={() => editItem(item)}></td>

                    </td>
                    <td className='col-2'>{item.createDate}</td>
                    <td className='col-2'>{item.available === true ? "" : " "}</td>
                    <td className='col-2'>{item.price}</td>

                    <td className='col-2'>{item.hebrewName}</td>


                  </tr>
                ))
                }
              </tbody>


            </div>

          </Table>



           */}


          <Table className='w-100'>
            <thead>
              <tr className='col-12 w-100' key={"header"}>
                <th className='col-2 lableForm ' value='hebrewName' id='hebrewName' onClick={e => setSortType('hebrewName')}> </th>
                <th className=' col-2 lableForm' value="price" id="price" onClick={e => setSortType("price")}> </th>
                <th className=' col-2 lableForm' value="available" id="available" onClick={e => setSortType("available")}></th>
                <th className='col-3 lableForm' value="createDate" id="createDate" onClick={e => setSortType("createDate")}> </th>

                <th className='col-1 lableForm  '></th>
                <th className='col-1 lableForm  '></th>
              </tr>
            </thead>


            <tbody className='table-responsive'>
              {categoryList.map((item) => (
                <>
                  <tr className=' bg-white   col-12' >
                    <td className=' border-0 col-2'>{item.hebrewName}</td>
                    <td className=' border-0 col-2'>{parseFloat(item.price ? item.price : 0).toFixed(2)} &#8362;</td>
                    <td className=' border-0 col-2'>{item.available === true ? "" : " "}</td>
                    <td className=' border-0 col-3' >{item.createDate}</td>
                    <td className='border-0 bg-transparent col-1' onClick={() => openDeleteMoodal(item._id)}><i class="fas fa-trash-alt "></i></td>
                    <td className='border-0 bg-transparent col-1' onClick={() => editItem(item)}></td>

                  </tr>
                  <tr className='bg-transparent' style={{ height: '15px' }}></tr>
                </>

              ))}
            </tbody>



          </Table>



        </div >
        <div className='col-md-1 p-0'></div>
        <div className='col-md-4  NewProduct  p-3 pb-0 bg-light' ><NewProduct product={productToEdit} /></div>
        {/* <div className='col-md-4  NewProduct  p-3 pb-0 bg-light' ><AddEdit /></div> */}


      </div>
    </div>
  )
  // }
}
const mapStateToProps = (state) => {
  return {

    products: state.productReducer.products,
    categories: state.categoryReducer.categories,
  };
}
const mapDispatchToProps = (dispatch) => ({
  getAllProducts: () => dispatch(actions.getAllProducts()),
  // createProduct: (product) => dispatch(actions.createProduct(product)),
  deleteProduct: (id) => dispatch(actions.deleteProduct(id)),
  updateProduct: (product) => dispatch(actions.updateProduct(product)),
  copyProduct: (id) => dispatch(actions.copyProduct(id)),
  getAllCategories: () => dispatch(actions.getAllCategories()),
  getProductByID: (id) => dispatch(actions.getProductByID(id)),
})
export default connect(mapStateToProps, mapDispatchToProps)(ProductList_manager)
// export default connect(mapStateToProps, mapDispatchToProps)(withRouter(ProductList))